.divClaim{
    
    
    font-family: 'B Biger Over',Helvetica,Arial,Lucida,sans-serif;
    font-size: 15px;
    color: #D4D4D4!important;
    letter-spacing: 1.0px;
    text-align: center;
    line-height: 40px;
}
  
  
  
.buttonClaim{
    color: #121212 !important;
    font-family: 'B Biger Over',Helvetica,Arial,Lucida,sans-serif;
    font-size: 15px;
    color: #8009c1;
    background-color: #7d0385;
    box-shadow: 0px 0px 5px 5px rgba(132, 72, 156, 0.597);
    background-image: linear-gradient(120deg,rgba(101, 24, 189, 0.829) -30%,rgba(255,255,255,0) 100%);
    letter-spacing: 1.0px;
    text-align: center;
    line-height: 30px;
    justify-content: center;
    border-radius: 5px;
    padding: 10px;
    box-sizing: border-box; 
    border: 2px solid rgb(164, 0, 164);
    position: relative;
    top: 5px;
    transition: box-shadow 0.1s ease-in-out;
}
.buttonClaim-clicked{
    box-shadow: 0px 0px 9px 9px rgba(8, 182, 182, 0.597);
    border-color: rgb(43, 255, 0);
    transform: scale(0.95);
    transition: box-shadow 0.5s ease-in-out;
    transition: scale 0.5s ease-in-out;
}
.buttonClaim:focus{
    outline:0;
}
   


